.checkbox {
  float: left;
  margin: 0;
  min-height: inherit;
  position: relative;
  margin-right: 20px;
  width: 100%;
  zoom: 1;
}
.checkbox:before,
.checkbox:after {
  content: "";
  display: table;
}
.checkbox:after {
  clear: both;
}
.checkbox:last-child {
  margin-right: 0;
}
.checkbox input[type="checkbox"] {
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  margin: 0;
}
.checkbox input[type="checkbox"]:checked + .checkbox__check + .checkbox__label {
  font-weight: 500;
}
.checkbox input[type="checkbox"]:checked + .checkbox__check .icon {
  display: inline-block;
}
.checkbox__check {
  background-color: transparent;
  border: solid 1px #cbcbcb;
  border-radius: 3px;
  height: 20px;
  margin: 0;
  margin-right: 10px;
  position: relative;
  text-align: center;
  transition: border 0.3s;
  float: left;
  width: 20px;
}
.checkbox__check .icon {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}
.checkbox__check .icon:before {
  width: 20px;
  height: 20px;
  line-height: 20px;
}
.checkbox__label {
  color: #535354;
  float: left;
  font-size: 15px;
  font-weight: normal;
  line-height: 18px;
  text-align: left;
  transition: color 0.3s;
  user-select: none;
  cursor: default;
}
.checkbox:hover .checkbox__check {
  border-color: #a7cde3;
}
