.main-menu {
	position: absolute;
	top: 0;
	z-index: 1;
	&__title {
		margin: 0;
		transform: rotate(-2deg);
		position: absolute;
		.lobster();
	}
	&__list {
		list-style: none;
		padding: 0;
		margin: 0;
		position: absolute;
		.clearfix();
	}
	&__item {
		float: left;
		padding: 0 13px;
		.main-menu__list {
			position: absolute;
			display: none;
		}
		&:last-of-type {
			margin-right: 0;
		}
		&:hover{
			&.with-sub>.main-menu__link:after{
				content: '';
				position: absolute;
				bottom: 3px;
				left: 50%;
				border: 10px solid transparent;
				border-bottom-color: white;
				margin-left: -5px;
			}
			.main-menu__list {
				display: inline-block;
			}
		}
	}
	&__link {
		color: #3f4512;
		font-size: 13px;
		font-weight: 500;
		text-decoration: none;
		line-height: 42px;
		display: inline-block;
		position: relative;
		&.active {

		}
	}
	&_green {
		.img('menu_green.png');
		top: 0;
		.main-menu__title {
			margin-left: 70px;
			transform: rotate(-2deg);
			top: 5px;
		}
		.main-menu__list {
			margin-top: 15px;
			margin-left: 30px;
			transform: rotate(-2deg);
			top: 30px;
		}
		.main-menu__item {
			.main-menu__list {
				margin: 0;
				padding-left: 30px;
				transform: rotate(0deg);
				left: -3px;
				top: 37px;
				.img('submenu_green.png');
				.main-menu__link{
					opacity: 0.8;
					color: #373534;
					font-size: 13px;
					font-weight: 400;
					&:hover{
						opacity: 1;
					}
				}
			}
		}
	}
	&_blue {
		top: 2px;
		.img('menu_blue.png');
		.main-menu__title {
			margin-top: 10px;
			margin-left: 90px;
			transform: rotate(-2.4deg);
			top: 0;
		}
		.main-menu__list {
			margin-top: 15px;
			margin-left: 20px;
			transform: rotate(-2.4deg);
			top: 32px;
		}
		.main-menu__item {
			.main-menu__list {
				margin: 0;
				padding-left: 30px;
				transform: rotate(0deg);
				left: -10px;
				top: 38px;
				.img('submenu_blue.png');
			}
		}
	}
	&_left {
		left: 0;
	}
	&_right {
		right: 0;
	}
}
