.vendor{
	padding: 20px 0;
	background: #efefef;
	&>.container{
		padding: 0 20px;
	}
	&__logo{
		vertical-align: middle;
	}
	&__text{
		font-size: 12px;
		line-height: 20px;
		color: #363537;
		font-weight: 400;
		display: inline-block;
		vertical-align: middle;
		margin-left: 20px;
		.icon{
			vertical-align: bottom;
			margin-right: 5px;
		}
		&_grey{
			opacity: 0.6;
			color: #898989;
		}
	}
	&__link{
		color: #2b2b2b;
		font-weight: 500;
		text-decoration: underline;
	}
}