.benefit {
	.purple-blue-gradient();
	width: 100%;
	height: 380px;
	position: relative;
	text-align: center;
	&__inner {
		position: relative;
		padding: 30px;
		z-index: 5;
	}
	&__bg {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		opacity: .05;
		z-index: 1;
	}
	&__img {
		width: 90px;
		margin-top: 32px;
	}
	&__title {
		font-size: 20px;
		font-weight: 300;
		line-height: 26px;
		margin: 13px 0 14px;
	}
	&__text {
		font-size: 13px;
		font-weight: 400;
		line-height: 20px;
	}
	&__shevron {
		position: absolute;
		bottom: 27px;
		z-index: 10;
		&_left {
			left: 37px;
		}
		&_right {
			right: 37px;
		}
	}
}
