.jumbotron {
	height: 480px;
	margin: 40px 0;
	color: #fff;
	.container{
		padding: 0 40px;
	}
	&__img {
		height: 480px;
		background-size: cover;
		background-repeat: no-repeat;
		position: absolute;
		left: 0;
		right: 0;
		z-index: -1;
	}
	&__title {
		font-size: 24px;
		font-weight: 700;
		line-height: 20px;
		text-transform: uppercase;
		margin-bottom: 30px;
		margin-top: 170px;
		letter-spacing: 0.1em;
	}
	&__text {
		font-size: 15px;
		font-weight: 300;
		line-height: 26px;
		margin-bottom: 20px;
		width: 40%;
		letter-spacing: 0.02em;
	}
}
